  width: 1139px;
  height: 456px;
}
@media (max-width: 1439px) {
  .oval--horizontal {
    width: 678px;
    height: 272px;
  }
}
.oval--vertical {
  width: 540px;
  height: 951px;
}
.oval--small {
  scale: 0.64;
}
.oval--temporary {
  display: none;
}
@media (max-width: 1439px) {
  .oval--temporary {
    display: block;
  }
}
@media (max-width: 959px) {
  .oval--temporary {
    display: none;
  }
}
.oval--paua {
  background-color: #231b52;
}
.oval--lipstick {
  background-color: #a42d5d;
}
.oval--christalle {
  background-color: #442c5f;
}
.oval--absolute-top-right, .oval--margin-top-right {
  left: 50%;
  top: 0;
}
.oval--absolute-top-right {
  transform: matrix(-0.75, 0.66, 0.75, 0.66, 72, -87);
}
@media (max-width: 1439px) {
  .oval--absolute-top-right {
    transform: matrix(-0.75, 0.66, 0.75, 0.66, -110, -87);
  }
}
@media (max-width: 959px) {
  .oval--absolute-top-right {
    display: none;
  }
}
.oval--margin-top-right {
  transform: matrix(-0.75, 0.66, 0.75, 0.66, 222, 236);
}
@media (max-width: 1439px) {
  .oval--margin-top-right {
    transform: matrix(-0.75, 0.66, 0.75, 0.66, 0, 92);
  }
}
@media (max-width: 959px) {
  .oval--margin-top-right {
    transform: matrix(-0.94, 0.33, 0.95, 0.33, -367, -145);
    scale: 0.56;
  }
}
.oval--absolut-center-27deg {
  top: 826px;
  right: 50%;
  transform: rotate(27deg) translateX(-120%);
}
@media (max-width: 1439px) {
  .oval--absolut-center-27deg {
    top: 528px;
    transform: rotate(27deg) translateX(-54%);
  }
}
